.wrapper__menu {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.wrapper__image {
  padding: 0;
}

.wrapper__menu-inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.wrapper__title {
  color: var(--color-text-dark);
  font-size: 3.2rem;
  font-weight: 600;
  margin-bottom: 65px;
}

.wrapper__caption {
  color: var(--color-text-dark-tint);
  font-size: 1.9rem;
  font-weight: 600;
  margin-bottom: 8px;
}

.wrapper__image {
  width: 100%;
  object-fit: contain;
}

@media screen and (max-width: 900px) {
  .wrapper__caption {
    font-size: 1.45rem;
  }

  .wrapper__menu {
    display: flex;
    flex-direction: column;
    padding: 16px;
    padding-bottom: 48px;
    min-height: 100vh;
    min-height: -webkit-fill-available;
  }

  .wrapper__title {
    font-size: 2.6rem;
    margin-bottom: 0;
  }

  .wrapper__image {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .wrapper__image {
    max-width: 100%;
    max-height: 250px;
    margin-bottom: 20px;
  }

  .wrapper__menu-inner {
    flex-grow: 2;
    text-align: center;
    justify-content: space-between;
  }

  .wrapper__button {
    padding: 20px 0;
    width: 100%;
  }
}
